@font-face {
  font-family: gotBold;
  src: url(Gotham-Bold.otf);
}

@font-face {
  font-family: gotMed;
  src: url(Gotham-Medium.otf);
}


@font-face {
  font-family: gotLight;
  src: url(Gotham-Light.otf);
}
.player-wrapper {
  position: relative;
  padding-top: 56.25% /* Player ratio: 100 / (1280 / 720) */
}

.react-player {
  position: absolute;
  top: 0;
  left: 0;
}

.popUp{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  z-index: 1;
  background-color: rgba(0, 0, 0, 0.653);
  backdrop-filter: blur(10px);
}
.demo-width{
width: 70vw;
position: absolute;
top: 50%;
left: 50%;
transform: translate(-50%, -50%);
background-color: black;

}
/* Styles for the video element */
#video-background {
  width: 100%;
  height: 100%;
  object-fit: cover;
  
}
/* Styles for the video container */
.video-background-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  z-index: -1; /* To place it behind other content */

}

body {
  background: black;
}

.box {
  height: 100vh;

}
.texteEmail{
  position: absolute;
  font-family: gotBold;
  color: #FFDF66;
  font-size: 5vw;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}
.facebook{
  width: 5vw;
  cursor: pointer;
}
.container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}



.texteService {
  position: absolute;
  top: 7vh;
  right: 0;
  margin: 2%;

}

#texteService:hover {
  font-size: 5.15vh;
  transition: all .2s ease-in-out;

}

.Hover{
  font-family: gotBold;
  color: rgb(0, 0, 0);
  font-size: 5vh;
  text-align: center;
  display: inline-block;
  cursor: pointer;
  text-shadow:
  #FFDF66 1px 1px,
  #FFDF66 -1px 1px,
  #FFDF66 -1px -1px,
  #FFDF66 1px -1px;
  }


.notHover {
  font-family: gotBold;
  color: #FFDF66;
  text-align: center;
  font-size: 5vh;
  display: inline-block;
  cursor: pointer;
}

.notHoverDemo {
  font-family: gotBold;
  color: #FFDF66;
  text-align: center;
  font-size: 5vh;
  display: inline-block;
  cursor: pointer;
}
