  position: absolute;
  top: 0;
  right: 0;
  margin: 2%;

}

#texteApro:hover {
  font-size: 5.15vh;
  transition: all .2s ease-in-out;
}

.texteContact {
  position: absolute;
  top: 14vh;
  right: 0;
  margin: 2%;

}

#texteContact:hover {
  font-size: 5.15vh;
  transition: all .2s ease-in-out;

}

.texteApro {
  font-family: gotBold;
  color: #FFDF66;
  text-align: justify;
