.button {
  background-color:#FFDF66; /* Green */
  border: none;
  color: white;
  padding: 16px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  transition-duration: 0.4s;
  cursor: pointer;
}

.button1 {
  background-color: transparent; 
color: #FFDF66;
font-family: gotBold;
  border: 2px solid #FFDF66;
}

.button1:hover {
  background-color: #FFDF66;
  color: black
}

@media only screen and (max-width: 600px) {

  .texteDemo{
color: #FFDF66;
  }


  .texteService{
    position: absolute;
    top: 4vh;
    right: 0;
    margin: 2%;
  }

  #texteService:hover {
    font-size: 3vh;

  }

  .notHover {
    font-size: 3vh;
  }
  .notHoverDemo {
    font-size: 3vh;
  }
  .Hover {
    font-size: 3vh;
  }
  #texteApro:hover {
